.dashboard {
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.dashboard .dashboardContainer {
    background-color: var(--color-dark-gray);
    height: 92vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.dashboard .userName, 
.dashboard .timeDisplay,
.dashboard .resultEntry {
    color: var(--color-white);
}

.dashboard .timeDisplay,
.dashboard .clockStatus {
    font-size: 1.5rem;  
    margin: 1rem 0;
}

.dashboard .clockStatus {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 1.25rem;
    padding: 0.3125rem 0.9375rem;
    border-radius: 0.3125rem;
    margin-top: 1.25rem;
}

.dashboard .clockStatus.ClockedIn {
    color: var(--color-green); 
}

.dashboard .clockStatus.ClockedOut {
    color: var(--color-red);
}

.dashboard .buttonGroup {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 1.25rem;
}

.dashboard .button {
    width: 7.5rem;
    height: 2.5rem; 
    margin: 0.625rem;
    padding: 0.625rem 1.25rem;
    background-color: var(--color-primary-blue);
    border: none;
    border-radius: 0.3125rem;
    color: var(--color-white);
    font-weight: bold;
    cursor: pointer;
    box-shadow: 0 0.25rem 0.375rem rgba(0, 0, 0, 0.2);
    transition: background-color 0.2s;
    display: flex;        
    justify-content: center; 
    align-items: center;  
}

.dashboard .button:hover {
    background-color: var(--color-dark-blue);
}

.dashboard .button:disabled {
    background-color: var(--color-lighter-gray);
    cursor: not-allowed;
}

.dashboard .resultEntry {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0;
}

.dashboard .resultEntryLine {
    margin: 0.625rem;
}

.errorMessage {
    color: var(--color-red);
    font-size: 14px;
    margin-top: 10px;
}

@media only screen and (max-width: 600px) {
    .dashboard .dashboardContainer {
        padding: 1.25rem; 
        height: auto;
    }

    .dashboard .timeDisplay,
    .dashboard .clockStatus {
        font-size: 1.25rem;
    }

    .dashboard .button {
        width: 80vw;
        margin: 0.3125rem 0;
        padding: 0.3125rem 0.625rem;
    }

    .dashboard .buttonGroup {
        flex-direction: column;
    }

    .dashboard .resultEntryLine {
        margin: 0.3125rem;
    }
}
